* {
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
  background-color: #7bd7e2;
}


.dropDown {
  width: 500px;
  margin: 100px auto;
}

.dropDown-btn {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  font-size: 20px;
  background: white;
  cursor: pointer;
}

.dropDown-content {
  margin-top: 5px;
  background: white;
}

.dropDown-item {
  padding: 15px;
  border-bottom: 2px solid whitesmoke;
  cursor: pointer;
}

.dropDown-item:hover {
  background: whitesmoke;
}
